import React from 'react';
import Picture from "./picture/Picture";
import connect from "react-redux/es/connect/connect";
import {fetchAlbums} from "../actions/albumActions";
import Loader from 'react-loaders';
import './albumContainer.less';
import 'loaders.css/src/animations/ball-grid-pulse.scss';
import ReactPaginate from 'react-paginate';

@connect(store => ({
    albums: store.albums,
    isLoadingAlbums: store.isLoadingAlbums
}), {
    fetchAlbums
})
class AlbumContainer extends React.Component {
    constructor(props) {
        super(props);
        this.perPages = 3;
        this.props.fetchAlbums();
    }

    handlePageClick(data) {
        console.dir(data.selected);
        this.props.fetchAlbums();
    }


    render() {
        if(!this.props.albums) return <Loader type="ball-grid-pulse" className='Data_loader'/>;
        const pictures = this.props.albums.map(album => <Picture key={album._id}
                                                                 animation='fade'
                                                                 album={album}/>);
        return <React.Fragment>
            {pictures}
            <div key="paginate" className="pagination_container"><ReactPaginate
                           previousLabel={"previous"}
                           nextLabel={"next"}
                           breakLabel={<a href="">...</a>}
                           breakClassName={"break-me"}
                           pageCount={this.pageCount}
                           marginPagesDisplayed={2}
                           pageRangeDisplayed={5}
                           onPageChange={(data) => this.handlePageClick(data)}
                           containerClassName={"pagination"}
                           subContainerClassName={"pages pagination"}
                           activeClassName={"active"}/></div>
        </React.Fragment>;
    }
}

export default AlbumContainer;


